.comments__block {
  background-color: #f8f8ff;
  margin-top: 15px;
  margin-left: 50px;
  padding: 10px;
  width: 68%;
  height: 200px;
  overflow-y: scroll;
}

.comments__NewComment {
  margin-left: 70px;
  margin-top: 10px;
  display: flex;
  flex-direction: row;
}

.comments__NewComment > span {
  flex: 0.1;
}

.button {
  margin: 10px;
  margin-left: 40px;
}

.button > button {
  background: #f0c14b;
  border-radius: 2px;
  width: 18%;
  height: 30px;
  border: 1px solid;
  font-weight: bolder;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
}

.comments__NewComment > form > input {
  width: 935px;
  height: 30px;
  margin-left: 40px;
  margin-top: 10px;
  border: 1px solid lightgray;
  border-radius: 1ch;
}

.comments__NewComment > input:focus {
  outline: none !important;
  border: 1px solid yellow;
  box-shadow: 0 0 10px #719ece;
}
