.product__root {
  position: relative;
  display: flex;
  background: white;
}

.search__bar {
  position: absolute;
  top: 0;
  left: 140px;
  width: 70.5%;
}

.product__ {
  flex: 1;
}

.product__details {
  margin-top: 50px;
  margin-left: 140px;
  margin-right: 130px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.product__details__image {
  flex: 2;
}

.product__details__image > img {
  object-fit: contain;
  height: 450px;
  width: 80%;
}

.product__details__info {
  flex: 3;
}

.product__details__rating {
  display: flex;
}

.product__details__price {
  color: #ab7a5f;
}

.product__details__description {
  line-height: 2;
  max-width: 100%;
}

.product__details__additionalDetails {
  line-height: 2;
  padding-top: 20px;
  max-width: 100%;
}

.product__details__additionalDetails > p > strong {
  float: left;
  width: 100px;
}

.product__details__button > button {
  background: #f0c14b;
  border-radius: 2px;
  width: 25%;
  height: 30px;
  border: 1px solid;
  font-weight: bolder;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
}

.product__commentsRoot {
  /* flex: 0.5; */
  margin-bottom: 20px;
}

.comment__title {
  margin-left: 45px;
}

.comment__title > p {
  font-size: x-large;
  font-weight: 600;
}
