.product {
  display: flex;
  flex-direction: column;
  background-color: white;
  z-index: 1;
  align-items: center;
  justify-content: flex-end;
  margin: 10px;
  padding: 20px;
  width: 100%;
  max-height: 400px;
  min-width: 100px;
}

.product:hover {
  box-shadow: rgba(0, 0, 0, 0.22) 0px 19px 43px;
  transform: translate3d(0px, -5px, 0px);
  cursor: pointer;
}

.product__info {
  height: 100px;
  margin-bottom: 150x;
}

.product__rating {
  display: flex;
}

.product__price {
  margin-top: 5px;
}

.product__image {
  position: relative;
  display: inline-block;
}

.product__image > img {
  max-height: 200px;
  width: 100%;
  object-fit: contain;
  margin-bottom: 15px;
}
.product__search {
  display: flex;
  position: absolute;
  opacity: 0;
}

.product__image:hover .product__search {
  width: 100%;
  height: 93%;
  background: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  opacity: 1;
  transition: all 0.3s linear;
}

.product > button {
  background: #f0c14b;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
  text-decoration: none;
}
