.footer__ {
  position: relative;
  height: 4rem;
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  align-items: center;
  background: #222;
  text-align: center;
}

.footer__ > h4 {
  color: white;
  margin: 0.1rem;
  font-weight: 400;
  text-transform: none;
  line-height: 1.25;
}

.footer__ > h4 > span {
  color: #f0c14b;
}
